import None from '../../public/noNumber'
import Detail from '../../public/detail.vue'
import Head from '../../public/head.vue'
import Loading from '../../public/Loading'
import test from '../../../static/js/language'
import LoadingImg from '../../public/loadingImg'
  export default {
      name: "homeSearch",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          isLoading:false,
          wonderfulList:[],
          hotList:[],
          goodVolunteer:[],
          actotal:0,
          language:[],
          isLocal:false,
          keyWord:''
        }
    },
    components:{
        Head,
      Detail,
      Loading,
      None,
      LoadingImg
    },
    methods:{
      toMore(val){
        if(val == 1){
          this.$router.push({
            path: '/experienceCentre',
            query:{
              information: this.keyWord
            }
          })
        }else if(val == 2){
          this.$router.push('/story')
        }else{
          this.$router.push('/volunteerList')
        }
      },
      goBack(){
        this.$router.go(-1)
      },
      getSearch(val){
        this.isLoading = true
        this.$http.post(this.api + '/home/Search/search',{
          token: localStorage.getItem('token'),
          keywords: this.$route.query.key,
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.hotList = res.data.data.activity.data
              this.wonderfulList = res.data.data.story.data
              this.goodVolunteer = res.data.data.user.data
              this.isLoading = false
              this.actotal = res.data.data.activity.total
              if(this.hotList.length || this.wonderfulList.length || this.goodVolunteer.length){
                this.isLocal = false
              }else{
                this.isLocal = true
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getSearch()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toStory(item,index){
        this.$router.push({
          path: '/storyPage',
          query:{
            information: item.story_id
          }
        })
      },
      addZan(item,index){
        let type = ''
        if(item.is_praise == 0){
          type = 1
        }else {
          type = 2
        }
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 1,
          table_id: item.story_id,
          type: type
        })
          .then(res=>{
            if(res.data.code == 1){
              item.praise_num = type == 2?item.praise_num - 1:item.praise_num + 1
              item.is_praise = type == 2? 0:1
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.addZan(item,index)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getStory(val){
        this.isLoading = true
        this.$http.post(this.api + '/home/Story/story_list',{
          token: localStorage.getItem('token'),
          keywords: this.$route.query.key,
          page:val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.wonderfulList = res.data.data.data
              this.isLoading = false
              if(this.wonderfulList.length){
                this.isLocal = false
              }else{
                this.isLocal = true
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getStory(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
        if(this.$route.query.type){
          this.getStory(val)
        }else{
          this.getSearch(val)
        }
      },
      activeCurrentChange(){

      },
      userCurrentChange(){

      },
      toPublish(item,index) {
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id
          }
        })
      },
      consult(item,index) {
        if (localStorage.getItem('isLogin')) {
          this.$router.push({
            path: '/counsel',
            query: {
              information: this.hotList[index]
            }
          })
        } else {
          this.$alert('您还没有注册或者登陆')
        }
      },
      toPerson(item){
        this.$router.push({
          path: '/userdata',
          query:{
            information: item.user_id
          }
        })
      }
    },
    mounted() {
        window.scroll(0,0)
        let _this = this
      if(this.$route.query.type == 1){
        _this.getStory(1)
      }else{
        _this.getSearch(1)
      }
    },
    created(){
        this.language = test()
      this.keyWord = this.$route.query.key
    }
  }



